nav {
  background: rgb(0, 0, 0, 0.3);
  width: max-content;
  display: block;
  padding: 0.75rem 1.75rem;
  z-index: 2;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  bottom: 2rem;
  display: flex;
  gap: 0.8rem;
  border-radius: 3rem;
  backdrop-filter: blur(15px);
  & a {
    background: transparent;
    padding: 1rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    &:hover {
      background: rgba(0, 0, 0, 0.3);
    }
    &.active {
      background: var(--color-prime);
      color: var(--color-bg);
    }
  }
}

.nav-icon {
  font-size: 1.25rem;
}
